"use client";

import { Code, Shield, Zap, Check } from "lucide-react";
import { motion } from "motion/react";

const steps = [
  {
    icon: Code,
    title: "Create Endpoint",
    description: "Define your metered API endpoint with pricing and payment requirements using our simple API.",
    color: "blue"
  },
  {
    icon: Shield,
    title: "Payment Verification",
    description: "Users send USDC on Base network. Our CDP facilitator verifies the on-chain transaction.",
    color: "purple"
  },
  {
    icon: Zap,
    title: "Access Granted",
    description: "Upon successful payment verification, the API endpoint becomes accessible to the user.",
    color: "green"
  }
];

const colorClasses = {
  blue: {
    bg: "bg-blue-500/10",
    border: "border-blue-500/20",
    text: "text-blue-400",
    gradient: "from-blue-500 to-cyan-500"
  },
  purple: {
    bg: "bg-purple-500/10",
    border: "border-purple-500/20",
    text: "text-purple-400",
    gradient: "from-purple-500 to-pink-500"
  },
  green: {
    bg: "bg-green-500/10",
    border: "border-green-500/20",
    text: "text-green-400",
    gradient: "from-green-500 to-emerald-500"
  }
};

export function HowItWorks() {
  return (
    <section id="how-it-works" className="py-20 px-6 bg-slate-950/50">
      <div className="container mx-auto max-w-6xl">
        <motion.div 
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl mb-4 text-white">
            How It Works
          </h2>
          <p className="text-xl text-slate-400 max-w-2xl mx-auto">
            Three simple steps to enable payment-required API endpoints
          </p>
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 relative">
          {/* Connection lines - hidden on mobile */}
          <div className="hidden md:block absolute top-16 left-0 right-0 h-0.5 bg-gradient-to-r from-blue-500/20 via-purple-500/20 to-green-500/20" />
          
          {steps.map((step, index) => {
            const colors = colorClasses[step.color as keyof typeof colorClasses];
            
            return (
              <motion.div
                key={step.title}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: index * 0.2 }}
                className="relative"
              >
                <div className="text-center">
                  <div className={`inline-flex items-center justify-center w-16 h-16 rounded-2xl bg-gradient-to-br ${colors.gradient} mb-6 relative z-10`}>
                    <step.icon className="h-8 w-8 text-white" />
                  </div>
                  
                  <div className={`absolute top-4 left-1/2 -translate-x-1/2 w-8 h-8 rounded-full ${colors.bg} border ${colors.border} flex items-center justify-center z-0`}>
                    <span className={`text-sm ${colors.text}`}>{index + 1}</span>
                  </div>
                  
                  <h3 className="text-2xl mb-3 text-white">
                    {step.title}
                  </h3>
                  
                  <p className="text-slate-400 leading-relaxed">
                    {step.description}
                  </p>
                </div>
              </motion.div>
            );
          })}
        </div>

        <motion.div 
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6, delay: 0.6 }}
          className="mt-16 p-8 rounded-2xl bg-gradient-to-br from-blue-500/10 to-purple-500/10 border border-blue-500/20"
        >
          <div className="flex items-start gap-4">
            <div className="flex-shrink-0 w-8 h-8 rounded-full bg-green-500/20 border border-green-500/30 flex items-center justify-center mt-1">
              <Check className="h-5 w-5 text-green-400" />
            </div>
            <div>
              <h4 className="text-xl text-white mb-2">
                API-First Architecture
              </h4>
              <p className="text-slate-400 leading-relaxed">
                All operations are accessible via RESTful APIs, making it easy to integrate NexFlow into your existing infrastructure and workflows. Complete SDKs available for popular languages.
              </p>
            </div>
          </div>
        </motion.div>
      </div>
    </section>
  );
}